$font-2:"kaushan Script" , cursive;

$Green      :#12a00d;
$Greenopa   :#12a00dda;
$Greenopas  :#12a00d4f;
$Grey-light :#afb2b3;
$Grey       :#808082;
$Grey-dark  :#5d5d5fd5;
$Grey-yellow:#686d4dd5;
$White      :#f7f7f7 ;
$White-opa  :#f7f7f7cb ;
$White-trans:#f7f7f763 ;
$Black      :#232323;
$Orange1    :#FF9B22;
$Orange2    :#FF8C00;
$Orange2opa :#ff8c00ab;
$Red        :#ff0000;
$Redopa     :#ff0000ab;
$Redopas    :hsla(0, 100%, 57%, 0.52);
$Redopass   :hsla(0, 100%, 57%, 0.35);
$Yellow     :rgba(255, 255, 0, 0.781) ;
$Bluesp     :#003b59;
$Bluespdark :#175473;
$Bluespd-opa:#17547388;
