$PaleBlue   :#00aaff83 ;
$PaleBlueopa:#00aaff49 ;

html{
    background: $White;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: $font-1;
}
body{
    
    background-repeat: no-repeat;
    background-size: auto;
    background: $White;
    overflow: hidden;
    width: 100%;
    height: 100vh;
    user-select: none;

}
#root{
    height: 100%;
    
    
}
a {
    font-size: xx-large;
    text-decoration: none;
    color: $Black;
    cursor: pointer;
    &:visited {
        color: $White-opa;
    }
    &hover{
        color:black;
    }
}

p{
    margin-block: 0;
    text-align: left;
    font-size: 20px;
    
}
h1{
    font-size: 50px;
    padding-bottom:30px;
    text-align: center;
}
h2{
    padding-bottom:30px;
    line-height: 4rem;
    text-align: center;
    font-size: 40px;
}
h3{
    text-align: left;
    font-size: 30px;
    font-weight: normal;
}
li{
    list-style: none;
}

    input{
        display: block;
    width: 100%;
    padding: .375rem .75rem;
    font-size: xx-large;
    line-height: 1.5;
    color: #495057;
    background-color: $White-opa;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    }
select{
    display: block;
    width : 200px;
    font-size:xx-large;
    line-height: normal;
    color: #495057;
    background-color: $White-opa;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
    
.no-cursor {
    cursor: none;
    user-select: none;
}
.row{
    display: flex;
    flex-direction: row;
}
.column{
    display: flex;
    flex-direction: column;
}
