@media only screen and (max-width: 1000px){
    body {
        height: 100vh;
        width: 100%;
        border-radius: 0px;
    }
    h1{
        font-size: 40px;
    }
    h2{
        font-size: 25px;
    }
    h3{
        font-size: 20px;
    }
    p{
        font-size: 15px;
    }
}
@media only screen and (max-width: 700px){
   
    h1{
        font-size: 40px;
    }
    h2{
        font: size 25px;
    }
    h3{
